.membership {
    align-items: center;
    justify-content: center;
    flex-direction: row;
    flex-wrap: wrap;
    width: 100%;
    display: flex;
}

.mem_left {
    /* padding: 20px; */
    display: flex;
    flex: 1;
    width: 100%;
    flex-direction: column;
    /* border-style: dashed; */
    width: 100%;
}

.mem_text {
    margin-top: 20px;
    font-size: 20px;
    text-align: justify;
    margin-bottom: 15px;
    letter-spacing: -0.015em;
}

.mem_right {
    display: flex;
    flex: 0.5;
    align-items: center;
    justify-content: center;
    /* padding: 20px; */
    width: 100%;
    margin-top: 28px;
    /* border-style: solid; */
}

.mem_content {
    font-family: Montserrat;
    font-style: normal;
    font-weight: bolder;
    font-size: 36px;
    min-width: 300px;
    line-height: 50px;
    letter-spacing: -0.015em;
}

.jointodaybutton {
    font-family: "Montserrat", sans-serif;
    border: none;
    color: white;
    background: #18a0fb;
    border-radius: 6px;
    font-weight: 500;
    font-size: 17px;
    width: auto;
    padding-left: 20px;
    padding-right: 20px;
    padding-top: 10px;
    padding-bottom: 10px;
    margin: 40px;
}

.button2 {
    background-color: white;
    border: 1px solid #18a0fb;
    box-sizing: border-box;
    border-radius: 6px;
    padding-left: 20px;
    padding-right: 20px;
    padding-top: 10px;
    padding-bottom: 10px;
    width: auto;
    color: #18a0fb;
}

@media screen and (max-width: 1200px) {
    .mem_text {
        font-size: 25px;
    }
    .jointodaybutton {
        margin: 10px;
        width: 150px;
    }
}

@media screen and (max-width: 800px) {
    .mem_right {
        flex: 1;
        margin-top: 0px;
        /* border-style: double; */
    }
    .mem_text {
        font-size: 16px;
        margin-bottom: 10px;
    }
}

@media screen and (max-width: 500px) {
    .membership {
        height: auto;
        margin: 0px;
        justify-content: center;
    }
}

@media screen and (max-width: 350px) {
    .mem_left {
        text-align: center;
    }
    .jointodaybutton {
        width: auto;
        margin: 5px;
    }
}