  overflow: hidden;
  width: 100%;
}
.carousel-container {
  position: relative;
  width: 60%;
}
.carousel-slide-list {
  position: relative;
  min-height: 45vh;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  min-height: 400px;
}
.carousel-slide-item {
  display: flex;
  flex-direction: column;
  position: absolute;
  transition: all 0.5s ease;
  width: 25rem;
}
.carousel-slide-item.s4 {
  transform: translateX(-60rem);
  opacity: 0;
}
.carousel-slide-item.s5 {
  transform: translateX(-30rem);
}
.carousel-slide-item.s1 {
  transform: translateX(0);
}
.carousel-slide-item.s2 {
  transform: translateX(30rem);
}
.carousel-slide-item.s3 {
  transform: translateX(60rem);
  opacity: 0;
}
.carousel-slide-item__img-link {
  height: 25rem;
  width: 100%;
  display: flex;
  cursor: zoom-in;
  overflow: hidden;
}
.carousel-slide-item__img-link:after {
  content: "read more";
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  position: absolute;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 0;
  transition: all 0.5s ease;
}
.carousel-slide-item__img-link:hover img {
  transform: scale(1.3);
}
.carousel-slide-item__img-link img {
  transition: all 0.5s ease;
}
.carousel-slide-item__img-link img {
  object-fit: cover;
  width: 100%;
  transition: all 0.5s ease;
  -webkit-filter: grayscale(1);
          filter: grayscale(1);
}
.carousel-slide-item__img-link img.active {
  -webkit-filter: none;
          filter: none;
}
