  height: 10%;
  position: absolute;
  bottom: -2.5rem;
}
.carousel-slide-item__body h4 {
  margin: 0.7rem 0 0 0;
  text-transform: uppercase;
}
.carousel-slide-item__body p {
  margin: 0.7rem 0 0 0;
  font-size: 1.2rem;
  line-height: 1.3;
}
.carousel-btn {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  z-index: 1;
  margin: 0;
  background: none;
  border: 0;
  outline: 0;
  cursor: pointer;
}
.carousel-btn.prev-btn {
  left: -5rem;
}
.carousel-btn.next-btn {
  right: -5rem;
}
.carousel-btn__arrow {
  border: solid #161616;
  border-width: 0 0.4rem 0.4rem 0;
  display: inline-block;
  padding: 3px;
  height: 6rem;
  width: 6rem;
}
.carousel-btn__arrow.left {
  transform: rotate(135deg);
}
.carousel-btn__arrow.right {
  transform: rotate(-45deg);
}
.carousel-dots {
  display: inline-block;
  margin-top: 2rem;
}
.carousel-dots .dot {
  width: 2rem;
  height: 2rem;
  background: #ccc;
  border: 0;
  border-radius: 50%;
  margin: 0 0.3rem;
  outline: none;
  transform: scale(0.5);
